  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  font-family: 'Assistant', sans-serif;
}

.header-text {
  color: #282b42;
  font-family: 'Assistant', sans-serif;
  font-size: 36px;
}

.subtext {
  /* font-weight: 600; */
  color: #727483;
  font-size: 28px;
  /* color: rgb(145, 145, 145); */
}

.link {
  font-weight: 600;
  color: #e36060;
  box-shadow: inset 0 -.0625rem 0 0 white,inset 0 -.1875rem 0 0 #ecb4b4;
  transition: box-shadow .15s ease;
}

.link:hover {
  transition: 0.2s ease;
  box-shadow: inset 0 -.0625rem 0 0 white,inset 0 -.1875rem 0 0 #e36060;
}

/* Title */
article h1 {
  color: #21242c;
  font-size: 30px;
  line-height: 1.1;
  margin-bottom: 8px;
  }

.projects-title h1 {
  color: #21242c;
  font-size: 28px;
  /* font-weight: 700; */
  line-height: 1.1;
}

/* project type  */
h6 {
  display: block;
font-family: 'Source Code Pro', monospace;  
font-size: 17px;
  margin-bottom: 8px;
  text-decoration: none;
  color: #e36060;
}

/* description */
.projects p, .about-body p, .footer-text {
  font-size: 17px;
  color: #837676;
}


.section-text h1 {
  color: #282b42;
  font-size: 34px;
  padding-bottom: 20px;
}


a {
  text-decoration: none;
}

.nav-block {
  width: 256px;
  display: block;
  box-sizing: border-box;
}
.navigation {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  position: fixed;
  z-index: 3;
  width: 256px;
  padding: 0 16px;
  align-items: flex-end;
}

.navigation a {
  margin-bottom: 16px;
  font-size: 18px;
  font-family: 'Source Code Pro', monospace;
  color: #4f505c;
  transition: color .2s ease;
  font-weight: 600;
}

.navigation a {
  border-bottom: 1.5px solid transparent;
  transition: 0.2s ease;
}

.navigation a:hover {
  border-bottom: 1.5px solid #ecb4b4;
  transition: 0.2s ease;
  color: #282b42;


}

.navigation a.active {
  color: #e36060;
}
