.header {
  align-items: center;
  display: flex;
  margin-left: 384px;
  margin-bottom: 32px;
  /* width: 256; */
}

.subtext {
  margin-top: 10px;
}

.header-image-wrapper,
.header-text-wrapper {
  width: 512px;
}

.header-image-wrapper {
  padding: 0 16px;
  margin-left: -128px;
}

.header-image {
  background-image: url(/static/media/prof.d47a40a7.png);
  /* background-color: red; */
  background-position: 50%;
  background-size: cover;
  border-radius: 8px;
  height: 550px;
  width: 530px;
}
.projects-wrapper{
  padding-top: 20px ;}

.projects {
  width: 90%;
  max-width: 900px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 20px;
  padding-bottom: 6vw;
}
.hi {
  grid-column: 1/3;
}
 
.card {
  min-height: 100%;
  background: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: #444;
  position: relative;
  top: 0;
  transition: all 0.3s ease;
}

.card h1 {
  -webkit-text-decoration: underline transparent;
          text-decoration: underline transparent;
}

.card:hover h1 {
  transition-property: -webkit-text-decoration-color;
  transition-property: text-decoration-color;
  transition-property: text-decoration-color, -webkit-text-decoration-color;
  -webkit-text-decoration-color: #e36060;
          text-decoration-color: #e36060;
  transition: 0.3s;
}
  

.card:hover .project-thumb {
  transition-property: opacity;
  opacity: 0.8;
  transition-duration: 0.3s;
}

.project-thumb {
  transition-property: opacity;
  opacity: 1;
  transition-duration: 0.3s;
}

.project-link {
  text-decoration: none;
  color: #444;
}
  
  
/* title and description */
 .card article {
  padding: 20px;
  display: flex;
  flex: 1 1;
  justify-content: flex-end;
  flex-direction: column;
}
.card .project-thumb {
  padding-bottom: 60%;
  background-size: cover;
  background-position: center center;
}

.card p, .about-body p{
  flex: 1 1; /* make p grow to fill available space*/
  line-height: 1.4;
}

/* typography */
h1 {
  font-size: 20px;
  margin: 0;
  color: #333;
}
  
  /* tag links */
.card span p{
font-size: 12px;
font-weight: bold;
color: #999;
text-transform: uppercase;
letter-spacing: 0.05em;
margin: 2em 0 0 0;
}

@media only screen and (min-width: 850px) {
  .projects{
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 850px) {
  .hi {
    grid-column: 1/2;
  
  }
}
.gallery-wrapper {
  margin-top: 30px;
  padding: 30px 0;
  margin-bottom: 90px;
}

.gallery {
  width: 90%;
  max-width: 900px;
  margin: 0 auto;
}

.column {
  box-sizing: border-box;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.column {
  flex: 50% 1;
  max-width: 50%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
}

@media screen and (max-width: 600px) {
  /* one image width */
  .column {
    flex: 100% 1;
    max-width: 100%;
  }
}

.about {
    display: flex;
    width: 90%;
  max-width: 900px;
    }
    
    .about-heading {
        width: 374px;
    }
    
    .about-body {
        width: 532px;
    }
    
    .about-body p:not(:last-child) {
        margin-bottom: 30px;
    
    }
    
    .about-wrapper {
        padding: 30px 0;
    }


.footer {
    margin-left:384px;
    display: flex;
    width:90%;
    max-width:900px;
    padding: 1.75rem 0px 1.75rem;

}


.footer-text {
    margin-left:auto; 
    margin-right:0
